import PartyClassService from '@/services/nucleus/PartyClassService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let PartyClassTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultPartyClassTableFilter: {
      class: String,
      default: null
    },
    defaultPartyClassTableOrderBy: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      partyClassTableData: [],
      partyClassTableFilterTimer: null,
      partyClassTableFilter: this.defaultPartyClassTableFilter,
      partyClassTableOrderBy: this.defaultPartyClassTableOrderBy,
      partyClassTableContainer: 'loading-container',
      partyClassTableBusy: false,
      partyClassTableTotalRows: 0,
      partyClassTableCurrentPage: 1,
      partyClassTablePerPage: 10
    }
  },
  computed: {
    partyClassTableNoDataText: function () {
      return this.partyClassTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    partyClassTableTotalPages: function () {
      return Math.ceil(this.partyClassTableTotalRows / this.partyClassTablePerPage)
    },
    partyClassTablePagingInfo: function () {
      return this.pagingInfo(this.partyClassTableCurrentPage, this.partyClassTablePerPage, this.partyClassTableTotalRows)
    }
  },
  methods: {
    loadPartyClassTableData (loadingContainerId) {
      let self = this
      this.partyClassTableBusy = true
      this.partyClassTableContainer = loadingContainerId || this.partyClassTableContainer
      this.startLoading(this.partyClassTableContainer)
      PartyClassService.partyClasses({
        pageIndex: this.partyClassTableCurrentPage - 1,
        pageSize: this.partyClassTablePerPage,
        filter: this.partyClassTableFilter,
        orderByField: this.partyClassTableOrderBy,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.partyClassTableContainer)
        self.partyClassTableBusy = false
        self.partyClassTableTotalRows = response.data.totalRows
        self.partyClassTableData = response.data.classificationList || []
      }).catch(function (error) {
        self.stopLoading(self.partyClassTableContainer)
        self.partyClassTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshPartyClassTable () {
      this.loadPartyClassTableData()
    },
    filterPartyClassTable (filter) {
      let self = this
      if (this.partyClassTableFilter !== filter) {
        clearTimeout(this.partyClassTableFilterTimer)
        this.partyClassTableFilterTimer = setTimeout(() => {
          self.partyClassTableFilter = filter
          self.loadPartyClassTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.partyClassTableFilterTimer)
      }
    },
    orderPartyClassTable (orderBy, active) {
      this.partyClassTableOrderBy = (active ? '' : '-') + orderBy
      this.loadPartyClassTableData()
    }
  },
  watch: {
    partyClassTableCurrentPage: function () {
      this.refreshPartyClassTable()
    },
    partyClassTablePerPage: function () {
      this.refreshPartyClassTable()
    }
  }
}

export default PartyClassTableMixin
